		mounted: function () {},

		methods: {
			async testFetch() {
                try {
                    const res = await fetch(api);
                    const data = await res.json();

					var lista_campos = data.results;
                    this.tabla_campos = lista_campos;
                } catch (error) {
                    console.log(error);
                }
			},
			async testFetch2() {
				try {
					this.show = true;

					const res = await fetch(api);
					const data = await res.json();
					var lista_campos = data.results;

					for (var i = 0; i < lista_campos.length; i++) {
						var numerillo = await this.getAldia(lista_campos[i].numero_campo);
						if (numerillo < 30 && numerillo >= 0) {
							lista_campos[i].aldia = "‚úî";
						} else {
							lista_campos[i].aldia = "‚ùå";
						}
					}
					//console.log(lista_campos[0].aldia);

					this.tabla_campos = lista_campos;
					this.btn_down_pdf = false; //Habilito los botones

					if (this.tabla_campos.length == 0) {
						this.msj_tabla = " No se encuentran regitros en esta tabla ";
					}
				} catch (error) {
					console.log(error);
				} finally {
					this.show = false;
				}
